import { useTranslation } from "react-i18next";
import NavBar from "../../../components/NavBar/NavBar";
import QuoteSteps from "../../QuoteSteps/QuoteSteps";

import "./ContactUs.css";

function ContactUs() {
  const { t } = useTranslation();
  return (
    <>
      <NavBar />
      <div className="contact-us">
        <div className="container">
          <div className="row d-flex justify-content-center pt-5 pb-5">
            <div className="col-sm-5 center">
              <h3 className="header-text-2">{t("contact.txt_1")}</h3>
              <img src="/images/other/contact-person.webp" alt="img"></img>
              <h5 className="header-text-2">{t("contact.txt_2")}</h5>
              <a href={`tel:${t("contact.txt_3")}`}>
                <p>{t("contact.txt_3")}</p>
              </a>
              <a href={`mailto:${t("contact.txt_4")}`}>
                <p>{t("contact.txt_4")}</p>
              </a>
            </div>
            <div className="col-sm-5 center">
              <h3 className="header-text-2">{t("contact.txt_5")}</h3>
              <div className="branch-address">
                <p class="address-p-tag">
                  <b>{t("contact.txt_6")}</b>
                  <br />
                  {t("contact.txt_7")}
                </p>
                <a href={`tel:${t("contact.txt_8")}`}>
                  <p>{t("contact.txt_8")}</p>
                </a>
              </div>
              <div className="branch-address">
                <p class="address-p-tag">
                  <b>{t("contact.txt_9")}</b>
                  <br />
                  {t("contact.txt_10")}
                </p>
                <a href={`tel:${t("contact.txt_11")}`}>
                  <p>{t("contact.txt_11")}</p>
                </a>
              </div>
              <div className="branch-address">
                <p class="address-p-tag">
                  <b>{t("contact.txt_12")}</b> {t("contact.txt_13")}
                </p>
                <p class="address-p-tag">
                  <b>{t("contact.txt_14")}</b> {t("contact.txt_15")}
                </p>
                <p class="address-p-tag">
                  <b>{t("contact.txt_16")}</b> {t("contact.txt_17")}
                </p>
                <img
                  src="/images/other/ForbeInternationalRealEstate-02.webp"
                  alt="logo"
                ></img>
                <p class="address-p-tag">{t("contact.txt_18")}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="row d-flex justify-content-center mt-5 mb-5">
        <div className="col-sm-12 col-md-12 col-lg-12 mt-5">
          <QuoteSteps stepOneSub={t("contact.txt_19")} />
        </div>
      </div>
    </>
  );
}

export default ContactUs;
